import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

//获取原型对象上的push函数
const originalPush = Router.prototype.push
//修改原型对象中的push方法
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const vueRouter = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Index',
      meta: {
        title: '表单类',
        nav: true,
      },
      component: () => import('@/views/Index'),
      redirect: `/user/output-formlist`,
      children: [{
        path: '/user/output-formlist',
        name: 'output-formlist',
        meta: {
          title: '表单列表'
        },
        component: () => import('@/views/User/form/formList'),
      }, {
        path: '/user/output-release',
        name: 'output-release',
        meta: {
          title: '导出发版内容'
        },
        component: () => import('@/views/User/form/outputRelease'),
      },]
    },
    {
      path: '/admin/editForm',
      name: 'admin-editForm',
      meta: {
        title: '编辑表单'
      },
      component: () => import('@/views/Admin/editForm')
    },
  ]
})
vueRouter.afterEach((to, from) => {
  document.title = to.meta.title || 'DM工具箱'
})
export default vueRouter



